@import "./default-variables.scss";

.main-preview {
  background-image: url("../img/background/hero-header.jpg");
  background-repeat: no-repeat;
}

.preview__list {
  @include reset-list();
}

.preview__link {
  @include fonts(700, 19px, #ffffff);
  line-height: 126%;
  text-decoration: none;
  background-color: $color-light-accent;

  &--accent {
    background-color: $color-dark-accent;
  }
}
