@import "./default-variables.scss";

.main-nav {
  @include outline;
  display: flex;
  flex-direction: column;
}

.main-nav__toggle {
  position: relative;

  display: flex;
  align-items: center;
  align-self: flex-end;

  width: 30px;
  height: 30px;
  padding: 0;

  background-color: transparent;

  cursor: pointer;
}

.main-nav__list {
  @include reset-list();
}

.main-nav__link {
  display: block;
  padding: 28px 32px;

  @include fonts(700);
  line-height: 141%;

  text-decoration: none;
}
